@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:ital@1&display=swap');


.card {
    
    width: 280px;
    height: 420px;
    box-shadow: -1px 2px 10px 4px;
    border-radius: 10px;
    padding: 0px;
    margin: 5px;
    overflow: hidden;
    border: 0px;
}

img{
    height: 50%;
}

table {
    position: absolute;
    bottom: 8px;
   width: 101%;
   padding: 0px;
   margin: 0px;
   
}

th,td{
    
    color: rgb(255, 255, 255);
    font-family: 'Chakra Petch', sans-serif;
    font-size: 1rem;
    margin:0px;
    border-right: 1px solid rgba(255, 255, 255, 0.418);
    text-align: center;
    
}



button{
    color: white;
    background-color: black;
    border-radius: 20%;
    margin: 5px;
}


.info_wrapper{
position:relative;
top: 0px;
 height: 100%;
 width: 100%;
 color: white;  
 font-family: 'Chakra Petch', sans-serif;
 padding: 20px;
 clip-path: polygon(0 0, 100% 1%, 100% 100%, 0 100%);
 background-color: #130f40;
 background-image: linear-gradient(315deg, #130f40 0%, #000000 74%);

}

.empty_team{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid rgba(255, 255, 255, 0.493);
    width: 85%;
    height:100%;
    border-style:dashed;

}

h1,h5{
    color: rgba(255, 255, 255, 0.993);
    font-weight: bold;
    font-family: 'Berkshire Swash', cursive;
    margin:4px;
}


.blink_me{
    animation: blinker 1s linear infinite;
}

@keyframes blinker{
    50%{
        opacity:0;
    }
}